* {
  margin: 0;
  padding: 0;
  border: none;
}

body {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.screen {
  width: 100vw;
  height: 100vh;
  background: #666666;
}

.lucky {
  display: none;
  position: absolute;
  background: #fff;
  margin: auto;
  border-radius: 3vw;
}

.lucky-wrapper {
  width: fit-content;
  max-width: 96vw;
  display: flex;
  flex-wrap: wrap;
}

.lucky-info {
  width: 24vw;
  height: 24vw;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.tip {
  width: fit-content;
  font-size: 3vw;
  margin: 2vw auto 0;
}

.avatar {
  width: 12vw;
  height: 12vw;
  border: 1px solid #ccc;
  border-radius: 1vw;
  background-size: cover;
  text-align: center;
  font-size: 8vw;
  line-height: 12vw;
  background-color: #ccc;
}

.name {
  font-size: 3vw;
  width: 22vw;
  padding: 0 1vw;
  overflow-x: auto;
  text-align: center;
}